import React from 'react';
import Footer from '../components/Footer';

import SiteHeader from '../components/SiteHeader';
import jay from '../components/IMG_2549.jpg';
// import './layout.css';
import ThemeContext from '../context/ThemeContext';
import { rhythm } from '../utils/typography';
import styled from 'styled-components';

const FlexContainer = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  transition: all 0.3s ease;
  ${props => {
    if (props.darkMode) {
      return `background-color: #2f3640;
      color: #fff;
      
      a, a:visited {
        color: rgb(73, 223, 255);
      }
      
      hr {
        background: #505050;
      }
      
      blockquote: {
        color: inherit;
        border-color: inherit;
      }
      `;
    }
  }}
`;

const MainContainer = styled.div`
  flex: 1;

  @media (min-width: 735px) {
    display: flex;
  }
`;

const LeftContainer = styled.div`
  @media (min-width: 735px) {
    margin: 0 0 10% 10%;
    flex-basis: 400em;
    flex-grow: 2;
  }
  margin: 0 0% 1% 0;
  max-width: 40em;
  padding: 0 ${rhythm(7 / 8)} ${rhythm(3 / 4)};

  h2:first-of-type {
    margin-top: 0;
  }
`;

const RightContainer = styled.div`
  @media (min-width: 735px) {
    margin: 0 10% 10% 0;
    flex-grow: 1;
  }
  margin: 0 0% 1% 0;
  max-width: 30em;
  padding: 0 ${rhythm(7 / 8)} ${rhythm(3 / 4)};
`;

const Image = styled.img`
  margin-top: 3em;
  filter: saturate(30%);
  transition: all 0.5s;
  :hover {
    filter: saturate(90%);
  }
`;

class AboutLayout extends React.Component {
  render() {
    const { location, title, children } = this.props;

    return (
      <ThemeContext.Consumer>
        {themeObj => (
          <FlexContainer darkMode={themeObj.darkMode}>
            <SiteHeader title={title} themeObj={themeObj} />
            <MainContainer>
              <LeftContainer>{children}</LeftContainer>
              <RightContainer>
                <Image src={jay}></Image>
              </RightContainer>
            </MainContainer>
            <Footer darkMode={themeObj.darkMode} />
          </FlexContainer>
        )}
      </ThemeContext.Consumer>
    );
  }
}

export default AboutLayout;
