import React, { Component } from 'react';

import standby from '../../content/assets/standby.png';
import sun from '../../content/assets/sun.png';
import styled from 'styled-components';

const StyledButton = styled.button`
  ${props => (props.theme.darkMode ? 'color: white;' : 'color: #333;')}
  margin-bottom: 2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding-left: 0;

  @media (min-width: 992px) {
    position: absolute;
    right: 10%;
    top: 3em;
  }
  :hover {
    span {
      width: 5.5em;
    }
  }
`;

const StyledLabel = styled.span`
  vertical-align: top;
  font-size: 10pt;
  margin-top: 0.3em;
  display: inline-block;
  width: 0;
  transition: 0.3s;
  white-space: nowrap;
  overflow: hidden;
`;

const Emoji = styled.span`
  padding-left: 0.5rem;
  img {
    height: 28px;
    width: 28px;
    display: inline-block;
    margin: 0;
    vertical-align: top;
    margin-top: 0.2rem;
  }
`;

const DarkmodeSwitcherButton = props => {
  return (
    <StyledButton
      className="darkmode-switcher"
      onClick={props.theme.toggleDarkMode}
      theme={props.theme}
    >
      {props.theme.darkMode ? (
        <div>
          <StyledLabel>Light mode</StyledLabel>
          <Emoji>
            ️<img src={sun} alt="Wake Icon" />
          </Emoji>
        </div>
      ) : (
        <div>
          <StyledLabel>Dark mode</StyledLabel>
          <Emoji>
            ️<img src={standby} alt="Windows 98 Standby Icon" />
          </Emoji>
        </div>
      )}
    </StyledButton>
  );
};

export default DarkmodeSwitcherButton;
