import { Link } from 'gatsby';
import React from 'react';
import styled from 'styled-components';
import DarkModeSwitcherButton from '../components/DarkmodeSwitcherButton';
import Menu from '../components/Menu';
import { rhythm } from '../utils/typography';

const Header = styled.h1`
  font-size: 1.7rem;
  font-family: 'Gugi', -apple-system, 'BlinkMacSystemFont', 'Segoe UI', 'Roboto',
    'Helvetica Neue', 'Arial', 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  transition: all 0.3s ease;
  ${props => {
    if (props.darkMode) {
      return `
  
  a, a:visited {
    color: #fff !important;
  }`;
    }
  }}
`;

const StyledLink = styled(Link)`
  box-shadow: none;
  text-decoration: none;
  color: inherit;
`;

const Div = styled.div`
  @media (min-width: 735px) {
    margin: 0 10%;
  }
  margin: 0 1%;
  max-width: ${rhythm(24)};
  padding: 0 ${rhythm(3 / 4)};
`;

const SiteHeader = ({ themeObj, title }) => {
  return (
    <Div>
      <Header darkMode={themeObj.darkMode}>
        <StyledLink to={`/`}>{title}</StyledLink>
      </Header>
      <Menu />
      <DarkModeSwitcherButton theme={themeObj} />
    </Div>
  );
};

export default SiteHeader;
